<template>
    <div class="amount">
        <div class="title">{{title}}</div>
        <div :class="`amount-block ${color} ${classOverride}`">
            <img class="amount-image" src="@/assets/newImages/coins/usdt-trc20.png">
            <div class="amount">{{formattedAmount}} USDT</div>
        </div>
        <div class="withdraw-block absolute" v-if="classOverride === 'withdraw-available'">
            <div class="withdraw-left-block">
                <div class="available-to-withdraw">{{$localizeFilter('available_to_withdraw', [`${$utils.formatNumber(availableProfitToWithdraw, 'en-US', 6, 6)} USDT`])}}</div>
                <div class="comission">{{$localizeFilter('comission_1_usdt')}}</div>
            </div>
            <button class="underline-button" @click="liquidityProvidingWithdrawProfit">
                {{$localizeFilter('withdraw')}}
            </button>
          
        </div>
    </div>
</template>

<script>
import { stringLiteral } from '@babel/types';
import { defineComponent } from '@vue/composition-api'
import anime from 'animejs/lib/anime.es.js';
import utils from '@/utils/utils'
import localizeFilter from '../filters/localize.filter';

export default defineComponent({
    props: {
        classOverride: {
            type: String,
            default: ''
        },
        amount: {
            type: Number,
            required: true
        },
        availableProfitToWithdraw: {
            type: Number,
            default: 0
        },
        title: {
            type: String,
            required: true
        },
        color: {
            type: String,
            required: true
        },
        type: {
            type: String,
            default: null
        }
    },
    watch: { 
        amount(newVal, oldVal) {
            // console.log('updated amount, oldVal =', oldVal, ', newVal =', newVal)
            if ((newVal !== oldVal)) {
                this.$nextTick(()=>{
                    if (this?.type === 'income') {
                        if (this.index>0) {
                            this.count = oldVal
                            this.setCount(newVal)
                        } else {
                            this.index=1
                            this.count = newVal
                        }
                    } else {
                        this.count = newVal
                    }
                })
            }
        }
    },
    data: ()=> ({
        count: 0.0,
        index: 0
    }),
    methods: {
        setCount (val) {
            const obj = { n: this.count }
            anime({
                targets: obj,
                n: val,
                round: 1,
                duration: 3000,
                easing: 'linear',
                update: () => {
                this.count = obj.n
                }
            })
        },
        async liquidityProvidingWithdrawProfit() {
            try {
                await this.$store.dispatch('liquidityProvidingWithdrawProfit')
                this.$message(localizeFilter('liquidity_withdraw_successfull'))
                this.$emit('reloadData')
            }
            catch (e) {
                 this.$error(e)
            }

        },
    },
    computed: {
        formattedAmount() {
            return (this.count/1000000).toFixed(6)
            // return this.count ? this.count.toString().slice(0, this.count.toString().length - 6) + '.' + this.count.toString().slice(this.count.toString().length - 6, this.count.toString().length) : this.count;
        }
    },
    mounted() {
        // console.log('mounted, amount =', this.amount)
        if (this.type !== 'income') {
            this.count = this.amount
        }
     },
})
</script>

